
body {
  margin: 0;
  background-color: #eeeced;
}
div {
  -webkit-tap-highlight-color: transparent;
}
input {
  outline: none;
}
a {
  text-decoration: none;
}
a:active {
    opacity: 0.4;
}
span, input {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
  font-weight: 300;
  color: #333;
  -webkit-font-smoothing: antialiased;
}
.tabbar {
  font-size: .8125rem;
}
.tabbar #icon {
    font-size: 1.375rem;
}



